export default {
  data() {
    return {
      list: [{
        storeName: '优逸smilehome自营',
        goodArr: []
      }],
      total_price: 0,         // 总计算钱数
      checked_sum: 0,         // 选中商品数量
      all_checked: false,     // 全部选中
    }
  },
  onLoad() {
    // //  获取购物车数据
    // this.getList()
  },
  onShow() {
    //  获取购物车数据
    this.getList()
  },
  methods: {
    // 获取购物车列表
    getList() {
      let that = this
      that.$http({
        url: that.$api.shopcarList,
        cb: (err, res) => {
          let list =  res.data.goods_list
          list.map((item, index) => {
            console.log(item)
            item.checked = false
          })
          console.log(list)
          this.list[0].goodArr = list
        }
      })
    },
    
    // 勾选商品
    checkboxChange(id, num, index) {
      // console.log(id, num, index)
      let checked = this.list[index].goodArr[num].checked
      // console.log(this.list[index].goodArr[num].checked)
      this.list[index].goodArr[num].checked = !checked
      // 计算总金额
      this.computePrice()
    },
    
    // 计算总金额
    computePrice() {
      let that = this
      let list =  this.list[0].goodArr
      that.total_price = 0
      that.checked_sum = 0
      list.map((item, index) => {
        if(item.checked) {
          that.checked_sum += Number(item.total_num)
          that.total_price += Number(item.goods_price) * Number(item.total_num)
        }
      })
    },
    
    // 选中所有商品
    selAllRadio() {
      let that = this
      if(that.all_checked) {
        that.list[0].goodArr.map((item, index) => {
          item.checked = false
        })
      } else {
        that.list[0].goodArr.map((item, index) => {
          item.checked = true
        })
      }
      this.all_checked = !this.all_checked
      
      // 计算总金额
      this.computePrice()
      
    },
    
    // 加减购物车物品数量
    controlNum(good, index, type) {
      let that = this
      let url = that.$api.addcar
      let num = that.list[0].goodArr[index].total_num
      let data = {
        goods_id: good.goods_id,
        goods_sku_id: good.goods_sku_id
      }
      
      // 减少数量
      if (type === 'cut' && num > 1) {
        url = that.$api.subcar
        that.$http({
          url: url,
          methods: 'POST',
          data: data,
          cb: (err, res) => {
            if (!err && res.code === 1) {
              that.list[0].goodArr[index].total_num -= 1
              
              // 计算总金额
              this.computePrice()
              return
            }
          }
        })
      }
      
      // 增加数量
      if (type === 'add') {
        data.goods_num = 1
        that.$http({
          url: url,
          methods: 'POST',
          data: data,
          cb: (err, res) => {
            if (!err && res.code === 1) {
              that.list[0].goodArr[index].total_num += 1
              
              // 计算总金额
              this.computePrice()
              return
            } else if (res.code === 0) {
              uni.showToast({
                title: res.msg,
                icon: 'none'
              })
            }
          }
        })
        return
      }
    },

    goShopping() {
      uni.switchTab({
        url: '../market/market'
      })
    },
    goDetail(goods_id) {
      uni.navigateTo({
        url: '../components/goodDetail?goods_id=' + goods_id
      })
    }
  }
}
